import logo from "../../static/img/logo.png"
import {
	mapState,
	mapActions,
} from 'vuex'
export default {
	name: 'Start',
	data() {
		return {
			name: '安徽石油商户中心',
			src: logo
		}
	},
	onShow() {
		console.log(this.hasLogin)
		if (this.hasLogin == false) {
			const timer = setTimeout(function() {
				uni.navigateTo({
					url: '../login/login',
					success: function() {
						clearTimeout(timer)
					}
				})
			}, 2000)

		} else {
			const timer = setTimeout(function() {
				uni.switchTab({
					url: 'index',
					success: function() {
						clearTimeout(timer)
					}
				})
			}, 2000)
			 this.getNewInfo();
		};
		
	},
	methods: {
		
		//app退出再次进入时获取最新信息
		getNewInfo() {
		const that = this;
			this.test.post('user/get_base_data',{
				Token:this.Token
			}).then(res => {
				// console.log(res)
				// debugger
				// uni.setStorageSync('userInfo',res.data.value)
				uni.setStorage({
				    key: 'userInfo',
				    data: res.data.value,
				});
				
				
				
			}).catch(err => {
				console.log(err)
			})
		},

	},
	computed: {
		...mapState(["hasLogin", "userInfo", "roles",'Token'])
	},
}
